import React from 'react'
import { Box } from '@mui/system';

function TabPanel3(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            {...other}
            style={{
                backgroundColor: "#FFF",
                maxWidth: '100%',
                minHeight: props.height ?? 400,  
                overflow: 'hidden',
                padding: 1,
            }}
        >
            {value === index &&
                <Box sx={{ p: 0, m: 0, overflow: 'hidden' }}>
                    {children}
                </Box>
            }
        </div >
    );
}

export default TabPanel3
