import React from 'react';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

// react-icons 
import { FaTransgender } from 'react-icons/fa';

const Gender = (props) => {

    const [gender, setGender] = React.useState(props.gender.toLowerCase() ?? '');

    const genderOptions = [
        {
            value: "m",
            label: "Male"
        },
        {
            value: "f",
            label: "Female"
        }
    ];

    const handleChange = (value) => {

        setGender(value);

        if (props.setGender) {
            props.setGender(value)
        }

    }

    return (
        <div>
            <TextField
                select
                label="Gender"
                name="gender"
                size={props.size ?? "small"}
                variant={props.variant !== null ? props.variant : "outlined"}
                value={gender}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                InputProps={{
                    startAdornment: props.withAdornment ? <FaTransgender size={20} style={{ marginRight: 10, color: "#00BFA5" }} /> : ""
                }}
                inputProps={{MenuProps: {disableScrollLock: true}}}
            >
                {genderOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default Gender;