import React from 'react'
import { parseISO, format } from 'date-fns';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import Button from '@mui/material/Button';
import Paper from '@mui/material/Paper';

// Form Components
import Gender from './fields/Gender';
import CivilStatus from './fields/CivilStatus';
import Birthdate from './fields/Birthdate';
import ContactNumber from './fields/ContactNumber';

// react-icons

import { FaSave } from 'react-icons/fa';

export default function EmployeeForm() {

    const authRedux = useSelector((state) => state.auth.value);
    let profile = authRedux?.me ?? {};

    let bday = null;
    let mygender = "";
    let mycontactnum = "";

    if (profile?.birthdate !== undefined) {
        bday = format(parseISO(profile?.birthdate), "MMM dd, yyyy");
    }
    if (profile?.gender !== undefined) {
        mygender = profile?.gender.toLowerCase() === "m" ? "Male" : "Female";
    }

    if (profile?.contact_no !== undefined) {
        mycontactnum = profile?.contact_no?.replace("+63", "") ?? "";
    }

    const [isEdit, setIsEdit] = React.useState(false);

    // Basic Profile  
    const [lastName, setLastName] = React.useState(profile?.last_name ?? "");
    const [firstName, setFirstName] = React.useState(profile?.first_name ?? "");
    const [middleName, setMiddleName] = React.useState(profile?.middle_name ?? "");
    const [birthdate, setBirthdate] = React.useState(bday);
    const [gender, setGender] = React.useState(mygender);
    const [civilStatus, setCivilStatus] = React.useState(profile?.civil_status ?? "");
    const [contactNumber, setContactNumber] = React.useState(mycontactnum);

    const handleSubmit = (e) => {
        e.preventDefault();
        setIsEdit(false);
    }

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper square sx={{ p: 3.5, pt: 1, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1, mt: 2 }}>
                            <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                            {isEdit &&
                                <React.Fragment>
                                    <Button
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 75,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: -1,
                                                mr: 1,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#1976D2'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="primary"
                                    >
                                        <FaSave size={14} style={{ marginRight: 5 }} />
                                        Save
                                    </Button>
                                </React.Fragment>
                            }
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setLastName(e.target.value)}
                                value={lastName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="lastName"
                                label="Last Name"
                                placeholder="Last Name"
                                name="lastName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setFirstName(e.target.value)}
                                value={firstName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="firstName"
                                label="First Name"
                                placeholder="First Name"
                                name="firstName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setMiddleName(e.target.value)}
                                value={middleName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="middleName"
                                label="Middle Name"
                                placeholder="Middle Name"
                                name="middleName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            {!isEdit &&
                                <TextField
                                    value={birthdate ?? ""}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Birthdate"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={6}>
                            {!isEdit &&
                                <TextField
                                    value={gender}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Gender"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Gender setGender={setGender} gender={gender} variant="standard" isRequired={isEdit} />
                            }
                        </Grid>
                        <Grid item xs={6}>
                            {!isEdit &&
                                <TextField
                                    value={civilStatus}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Civil Status"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={6}>
                            {!isEdit &&
                                <TextField
                                    value={contactNumber}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Contact Number"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <TextField
                                    onChange={(e) => setContactNumber(e.target.value)}
                                    value={contactNumber}
                                    variant="standard"
                                    size="small"
                                    required
                                    fullWidth
                                    id="contactNumber"
                                    label="Contact Number"
                                    name="contactNumber"
                                    InputProps={{
                                        inputComponent: ContactNumber
                                    }}
                                />
                            }
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
