import React from 'react'
import { Box } from '@mui/system'; 

function TabPanel2(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            {...other}
            style={{
                overflow: 'hidden',
                backgroundColor: "#FFF",
                height: '100%',
                width: '100%',
                paddingRight: '75px'
            }}
        >
            {value === index && (
                <Box sx={{ flexGrow: 1 }}>
                    {children}
                </Box>
            )}
        </div>
    );
}

export default TabPanel2
