import React from 'react';
import { format } from 'date-fns';

// MUI Components 
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

// Page Components
import EmployeeForm from '../../forms/EmployeeForm';
import { FaRedo } from 'react-icons/fa';

export default function EmployeeProfilePage(props) {
    return (
        <div>
            <Box display="flex" flexDirection="row" sx={{ width: '100%' }}>
                <Box display="flex" flexDirection="column" justifyContent="flex-start" sx={{ backgroundColor: "#00BFA5", m: 1, mr: 0, width: '30%' }}>
                    <Paper sx={{ height: 110, p: 2, m: 1, display: "flex", flexDirection: "column" }}>
                        <Typography variant="caption">
                            Last Update
                        </Typography>
                        <Typography variant="h6">
                            {format(Date.now(), "MMMM dd, yyyy h:mm:ss a")}
                        </Typography>
                        <Box sx={{ width: '100%', p: 1, pl: 0, mt: -1, ml: -1 }}>
                            <Button sx={{ color: "#00BFA5", "&:hover": { backgroundColor: 'transparent' } }}>
                                <FaRedo style={{ marginRight: 4 }} />
                                Refresh
                            </Button>
                        </Box>
                    </Paper>
                </Box>
                <Box sx={{ m: 1, width: '70%', minHeight: 100 }}>
                    <EmployeeForm />
                </Box>
            </Box>
        </div>
    )
}
