import React from 'react';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import { FaUserFriends } from 'react-icons/fa';




const CivilStatus = (props) => {

    const [civilStatus, setCivilStatus] = React.useState(props.civilStatus.toLowerCase() ?? '');

    const civilStatusOptions = [
        {
            value: "single",
            label: "Single"
        },
        {
            value: "married",
            label: "Married"
        },
        {
            value: "widowed",
            label: "Widowed"
        },
        {
            value: "divorced",
            label: "Divorced"
        },
        {
            value: "separated",
            label: "Separated"
        },
    ];

    const handleChange = (value) => {

        setCivilStatus(value);

        if (props.setCivilStatus) {
            props.setCivilStatus(value)
        }

    }

    return (
        <div>
            <TextField
                select
                label="Civil Status"
                name="civilStatus"
                size={props.size ?? "small"}
                variant={props.variant !== null ? props.variant : "outlined"}
                value={civilStatus}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                InputProps={{
                    startAdornment: props.withAdornment ? <FaUserFriends size={22} style={{ marginRight: 10, color: "#00BFA5" }} /> : ""
                }}
                inputProps={{MenuProps: {disableScrollLock: true}}}
            >
                {civilStatusOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default CivilStatus;