import * as React from 'react';

// MUI Components
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';

// Page Components
import TabPanel from './components/TabPanel3';

// options
import employeeprofiletabs from './options/employeeprofiletabs';

export default function UserProfileTabs(props) {

    const options = employeeprofiletabs;
    const [value, setValue] = React.useState(1);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ maxWidth: '100%', mt: 0, bgcolor: 'background.paper' }}>
            <Tabs
                value={value}
                sx={{ p: 1, pt: 0 }}
            >
                {
                    options.map((data, index) => {
                        return <Tab onClick={() => handleChange(index)} key={`tabkey-${index}`} label={
                            <Typography variant="caption1">
                                <Box display="flex">
                                    {data.icon}
                                    {data.name}
                                </Box>
                            </Typography>
                        } />
                    })
                }
            </Tabs>
            {
                options.map((data, index) => {
                    return <TabPanel value={value} index={index} key={`tabpanelkey-${index}`}>
                        {data.content}
                    </TabPanel>
                })
            }
        </Box>
    );
}
