
import React from 'react';
import { store } from '../../../redux/store';
import _ from 'lodash';

// Services
import toastService from '../../../services/toast/toastService';
import apiRequest from '../../../services/api/apiService';
import responseService from '../../../services/response/responseService';


// MUI Component
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import LoadingButton from '@mui/lab/LoadingButton';
 
export default function EnterPinForm(props) {

    const [pin, setPin] = React.useState("");
    const [loading, setLoading] = React.useState(false);

    const handleChange = (value) => {

        let newPin = pin;

        if (pin.length < 6) {
            newPin = `${newPin}${value}`;
            setPin(newPin);
        }

    }

    const handleClear = () => {
        setPin("");
    }

    const check = async (pin) => {
        setLoading(true);
        try {
            let data =  {
                pin
            }

            const token = store.getState().auth.value.token;
            let response = await apiRequest({token: token}).post('checkpin', data);
            if (response ?? true) {
                if (response.status === 200) { 
                    if(response.data.isValid){
                        return true;
                    }else{
                        return false;
                    }                    
                }
            } else {
                throw new Error('No Server Response');
            }
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    }

    const handleSubmit = async () => {

        if (pin.length < 6) {
            toastService.alert('Please enter a valid PIN');
            return false;
        } 
        
        if (props.verify !== undefined && props.verify) {
            let result = await check(pin);
            setLoading(false);
            if (result) {
                if (props.handleSubmit) {
                    props.handleSubmit(pin);
                }
            }else{
                toastService.error('Invalid PIN');
            }
        }else{
            if (props.handleSubmit) {
                props.handleSubmit(pin);
            }
        }

    }

    return (
        <div style={{ width: 400 }}>
            <Box sx={{ width: '100%', p: 1 }}>
                <Paper sx={{ width: '100%', p: 1 }}>
                    <Box display="flex" flexDirection="row" justifyContent="space-evenly">
                        <TextField
                            type="password"
                            size="small"
                            fullWidth
                            sx={{
                                input: {
                                    textAlign: 'center',
                                    fontSize: '35px',
                                    maxLength: '1'
                                }
                            }}
                            onKeyDown={(e) => {
                                let newPin = pin;
                                if (isFinite(parseInt(e.key)) && parseInt(e.key) >= 0) {
                                    let key = e.key;
                                    if (pin.length < 6) {
                                        newPin = `${newPin}${key}`;
                                        setPin(newPin);
                                    }
                                } else {
                                    if (e.key === "Enter") {
                                        handleSubmit();
                                    }
                                }
                            }}
                            value={pin}
                            inputProps={{
                                max: 6
                            }}
                            autoFocus
                        />
                    </Box>
                    <Box>
                        <Button sx={{ mt: 1, mb: 0 }} onClick={handleClear}>Clear</Button>
                    </Box>
                    <Grid container spacing={1} sx={{ mt: 0, dispaly: 'flex' }}>
                        {
                            _.times(9, (i) => (
                                <Grid key={`gridKey${i}`} item xs={4} display="flex" justifyContent="center">
                                    <Button
                                        variant="contained"
                                        sx={{ alignItems: 'center' }}
                                        fullWidth
                                        onClick={() => handleChange(i + 1)}>
                                        {i + 1}
                                    </Button>
                                </Grid>
                            ))
                        }
                        <Grid item xs={4} display="flex" justifyContent="center">
                            <Button variant="contained" sx={{ alignItems: 'center' }} fullWidth onClick={() => handleChange(0)}>0</Button>
                        </Grid>
                    </Grid>
                </Paper>
                <LoadingButton variant="contained" sx={{ alignItems: 'center', mt: 1 }} fullWidth onClick={() => handleSubmit()} loading={loading}>Submit</LoadingButton>
            </Box>
        </div>
    )
}
