import * as React from 'react';
import config from '../../../config/config';

// Redux
import { useSelector } from 'react-redux';


// MUI Components 
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';

// Page Components
import TabPanel from './components/TabPanel2';
import UserProfileTabs from './UserProfileTabs';

// react-icons 
import { FaCogs, FaEdit } from 'react-icons/fa';
import { FcServices } from 'react-icons/fc';

export default function UserSettingsTabs(props) {
 
    const authRedux = useSelector((state) => state.auth.value);
    const profile = authRedux.me;
    let avatar_path = null;

    if (profile?.length > 0) {
        avatar_path = `${config.DOMAIN}users/${profile?.uuid}/files/avatar/${profile?.avatar}`;
    }

    const [value, setValue] = React.useState(0);
    
    const handleChange = (newValue) => {
        setValue(newValue);
    }; 

    return (
        <Box
            sx={{ display: 'flex', flexGrow: 1, flexDirection: 'row', overflow: 'hidden' }}
        >
            <Box sx={{ p: 0 }}>
                <Box display="flex" flexDirection="row" justifyContent="center" sx={{ p: 1, ml: 0, mr: -10, width: 225, backgroundColor: '#eee' }}>
                    <FcServices size={20} style={{ marginRight: 5, marginTop: 7, marginLeft: 5 }} />
                    <Typography variant="h6" sx={{ mt: 0.2 }}>
                        User Settings
                    </Typography>
                </Box>
                <Box display="flex" flexDirection="column" alignItems="center" sx={{ p: 1, pt: 0, ml: 0, width: '100%', borderRight: '2px solid #eee', borderBottom: '1px solid #eee' }}>
                    <Avatar src={avatar_path} sx={{ width: '100%', height: '100%', p: 1, mt: 1, mb: 1 }} variant="rounded" />
                    <Button
                        sx={{
                            mt: 1,
                            minWidth: 200,
                            borderRadius: 0,  
                        }}
                        variant="text"
                        component="label"
                        size="small"
                    >
                        <FaEdit size={11} style={{ marginRight: 5 }} />
                        Change Avatar
                    </Button>
                </Box>
                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    sx={{ height: '100vh', flexGrow: 1, width: 225, pt: 0, borderRight: '2px solid #eee' }}
                    TabIndicatorProps={{
                        style: {
                            right: 1,
                            height: 40,
                            marginTop: 5
                        }
                    }}
                >

                    <Tab onClick={() => handleChange(0)} sx={{ alignItems: "start", p: 0 }} label={
                        <React.Fragment>
                            <Box display="flex" flexDirection="column" textAlign="left" sx={{ pl: 2, width: '100%', borderBottom: '1px solid #ccc', }}>
                                <Typography variant="subtitle2" sx={{ m: 2, ml: 0 }}>
                                    <Box display="flex" flexDirection="row">
                                        <FaCogs size={18} style={{ marginRight: 10 }} />
                                        My Settings
                                    </Box>
                                </Typography>
                            </Box>
                        </React.Fragment>
                    } />                    
                </Tabs>
            </Box>
            <Box display="flex" flexGrow={1}>
                <TabPanel value={value} index={0} >
                    <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                        <UserProfileTabs />
                    </Paper>
                </TabPanel>                
            </Box>
        </Box >
    );
}
