import React from 'react'

// Redux

// MUI Components
import Typography from '@mui/material/Typography';

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import EnterPinForm from '../forms/EnterPinForm';

// react-icons
import { FaKey } from 'react-icons/fa';

export default function EnterPinModal(props) {

    const handleSubmit = (pin) => {
      
        if (props.handleSubmit) {
            props.handleSubmit(pin);
        }

    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={<Typography><FaKey style={{marginRight: 5}} />{props.title ?? "Enter PIN"}</Typography>} toggle={props.toggle}>
                <EnterPinForm handleSubmit={(pin) => handleSubmit(pin)} verify={props.verify ?? false} />
            </InnerModalDialog>
        </div>
    )
}
