import React from 'react';
import { format } from 'date-fns'; 

// Services
import utilityService from '../../../../services/utils/utilityServices'; 
import toastService from '../../../../services/toast/toastService';

// redux 
import { useSelector } from 'react-redux';

// MUI Components 
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

// Page Components 
import EditUserAccountForm from '../../forms/EditUserAccountForm';
import EnterPinModal from '../../modals/EnterPinModal';

// react-icons
import { FaEdit, FaRedo, FaUserCog } from 'react-icons/fa';
 
export default function EmployeeAccountPage(props) {

    const authRedux = useSelector((state) => state.auth.value); 
    const [newPin, setNewPin] = React.useState(''); 

    const [showOldPinModal, setShowOldPinModal] = React.useState(false);
    const toggleShowOldPinModal = () => setShowOldPinModal(!showOldPinModal);
    const [showNewPinModal, setShowNewPinModal] = React.useState(false);
    const toggleShowNewPinModal = () => setShowNewPinModal(!showNewPinModal);
    const [showVerifyPinModal, setShowVerifyPinModal] = React.useState(false);
    const toggleShowVerifyPinModal = () => setShowVerifyPinModal(!showVerifyPinModal);

    const handleOldPin = (pin) => { 
         if(pin.length === 6){ 
            toggleShowOldPinModal();
            toggleShowNewPinModal();
         }else{
            toastService.alert('Please enter a valid PIN');
            return false;
         }

    }

    const handleNewPin = (pin) => {
        if(true){
            setNewPin(pin);
            toggleShowNewPinModal();
            toggleShowVerifyPinModal();
        }
    }

    const handleVerifyPin = (pin) => {
        if(pin === newPin){
            // dispatch here
        }
    }

    return (
        <div>
             {
                showOldPinModal &&
                <EnterPinModal verify={true} title="Enter Old PIN" open={showOldPinModal} toggle={toggleShowOldPinModal} handleSubmit={(pin) => handleOldPin(pin)} />
            }
            {
                showNewPinModal &&
                <EnterPinModal title="Enter New PIN" open={showNewPinModal} toggle={toggleShowNewPinModal} handleSubmit={(pin) => handleNewPin(pin)} />
            }
            {
                showVerifyPinModal &&
                <EnterPinModal title="Verify PIN" open={showVerifyPinModal} toggle={toggleShowVerifyPinModal} handleSubmit={(pin) => handleVerifyPin(pin)} />
            }
            <Box display="flex" flexDirection="row" sx={{ width: '100%' }}>
                <Box display="flex" flexDirection="column" justifyContent="space-evenly" sx={{ backgroundColor: "#00BFA5", m: 1, mr: 0, width: '30%', minHeight: 200 }}>
                    <Paper sx={{ height: 110, p: 2, m: 1, display: "flex", flexDirection: "column" }}>
                        <Typography variant="caption">
                            Last Update
                        </Typography>
                        <Typography variant="h6">
                            {format(Date.now(), "MMMM dd, yyyy h:mm:ss a")}
                        </Typography>
                        <Box sx={{ width: '100%', p: 1, pl: 0, mt: -1, ml: -1 }}>
                            <Button sx={{ color: "#00BFA5", "&:hover": { backgroundColor: 'transparent' } }}>
                                <FaRedo style={{ marginRight: 4 }} />
                                Refresh
                            </Button>
                        </Box>
                    </Paper>
                    <Paper sx={{ height: 110, p: 2, m: 1, display: "flex", flexDirection: "column" }}>
                        <Typography variant="caption">
                            Role
                        </Typography>
                        <Typography variant="h6">
                            {utilityService.capitalizeFirstLetter(authRedux.role.role)}
                        </Typography>
                        <Box sx={{ width: '100%', p: 1, pl: 0, mt: -1, ml: -1 }}>
                            <Button sx={{ color: "#00BFA5", "&:hover": { backgroundColor: 'transparent' } }}>
                                <FaEdit style={{ marginRight: 4 }} />
                                Change
                            </Button>
                        </Box>
                    </Paper>
                    <Paper sx={{ height: 110, p: 2, m: 1, display: "flex", flexDirection: "column" }}>
                        <Typography variant="caption">
                            Pin
                        </Typography>
                        <Typography variant="h6">
                            ******
                        </Typography>
                        <Box disaply="flex" flexDirection="row" sx={{ width: '100%', p: 1, pl: 0, mt: -1, ml: -1 }}>
                            <Button onClick={toggleShowOldPinModal} sx={{ color: "#00BFA5", "&:hover": { backgroundColor: 'transparent' } }}>
                                <FaUserCog size={16} style={{ marginRight: 4 }} />
                                Change 
                            </Button>
                        </Box>
                    </Paper>
                </Box>
                <Box sx={{ m: 1, mt: 2, width: '70%', minHeight: 100 }}>
                    <EditUserAccountForm />
                </Box>
            </Box>
        </div>
    )
}
