import React from 'react';


// MUI Components  
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { MdPerson, MdVisibility, MdVisibilityOff } from 'react-icons/md';
import { FaEdit, FaUserEdit } from 'react-icons/fa';

export default function EditUserAccountForm() {

    const [username, setUsername] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [isShowPassword, setIsShowPassword] = React.useState(false);
    const toggleIsShowPassword = () => setIsShowPassword(!isShowPassword);

    const handleSubmit = (e) => {
        e.preventDefault();
    }

    return (
        <div>
            <Box sx={{ width: '100%' }}>
                <Paper square sx={{ p: 5, m: 1 }}>
                    <form onSubmit={(e) => handleSubmit(e)} noValidate autoComplete="off">
                        <Box display="flex" flexDirection="row">
                            <FaUserEdit size={22} style={{ marginTop: 4, marginRight: 4, marginLeft: 2 }} />
                            <Typography variant="h6">Edit Credentials</Typography>
                        </Box>
                        <Box>
                            <TextField
                                onChange={(e) => { setUsername(e.target.value); }}
                                value={username}
                                variant="outlined"
                                size="small"
                                margin="normal"
                                required
                                fullWidth
                                name="username"
                                id="username"
                                label="Username"
                                placeholder="Username"
                                autoFocus
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <MdPerson />
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </Box>
                        <Box sx={{ mt: -1 }}>
                            <TextField
                                type={isShowPassword ? "text" : "password"}
                                onChange={(e) => { setPassword(e.target.value); }}
                                value={password}
                                variant="outlined"
                                size="small"
                                margin="normal"
                                required
                                fullWidth
                                name="password"
                                label="Current Password"
                                placeholder="Current Password"
                                id="password"
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                                {isShowPassword ?
                                                    <MdVisibility size={15} />
                                                    :
                                                    <MdVisibilityOff size={15} />
                                                }
                                            </IconButton>
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </Box>
                        <Box sx={{ mt: -1 }}>
                            <TextField
                                type={isShowPassword ? "text" : "password"}
                                onChange={(e) => { setPassword(e.target.value); }}
                                value={password}
                                variant="outlined"
                                size="small"
                                margin="normal"
                                required
                                fullWidth
                                name="newpassword"
                                label="New Password"
                                placeholder="New Password"
                                id="newpassword"
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                                {isShowPassword ?
                                                    <MdVisibility size={15} />
                                                    :
                                                    <MdVisibilityOff size={15} />
                                                }
                                            </IconButton>
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </Box>
                        <LoadingButton
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            sx={{ mt: 2 }}
                        >
                            <FaEdit size={15} style={{ marginRight: 3 }} />
                            <Typography>Update</Typography>
                        </LoadingButton>
                    </form>
                </Paper>
            </Box>
        </div>
    )
}
